import $ from "jquery";
import "slick";

$(document).ready(function () {
  $(".slider").slick({
    arrows: true,
    dots: true,
    adaptiveHeight: true,
    slidesToShow: 1,
    slidesToScroll: 1,
    speed: 1000,
    easing: "ease",
    infinite: false,
    initialSlide: 0,
    autoplay: false,
    // autoplaySpeed: 3000,
    pauseOnDotsHover: true,
    pauseOnFocus: true,
    pauseOnFocus: true,
    draggable: true,
    swipe: true,
    touchThreshold: 10,
    touchMove: true,
    waitForAnimate: false,
    centerMode: true,
    variableWidth: false,
  });
});

